body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Grid Styling */
.Grid {
  margin: 0px;
  padding: 0px;
}

.container-fluid {
  margin: 0px;
  padding: 0px;
}
/* Styling on Elements Direct */
h1 {
  margin: 0px;
  padding: 0px;
}

p {
  font-size: 16px;
  font-family: "Roboto Mono", monospace;
}
@media (max-width: 768px) {
  /* small scale */
  .page {
  }

  .nav-bar {
    position: absolute;
    z-index: 1000;
    background-color: rgb(255, 208, 0);
  }

  .nav-bar__logo {
    text-align: center;
    font-size: 15px;
    margin: 5px 0px 5px 10px;
    color: rgb(36, 36, 36);
    font-size: 32px;
    font-family: "Bangers", cursive;
    text-align: left;
  }

  .nav-bar__logo:hover {
    color: rgb(56, 54, 54);
  }

  .nav-bar__button {
    display: none !important;
  }

  .home {
    padding-top: 100px;
    background-color: rgb(255, 208, 0);
    height: 90%;
  }

  .home__heading {
    text-align: center;
    font-size: 60px;
    margin-top: 30px;
    font-family: "Bangers", cursive;
  }

  .home__tagline {
    text-align: center;
    margin: 0px 10px 0px 10px;
  }

  .home__image-large {
    display: none;
  }

  .home__image-small {
    width: 50%;
  }

  .langbar {
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: white;
    box-shadow: 0px 0px 5px #cbd1d3;
  }

  .langbar__img {
    /* display: block; */
    height: 5px;
    margin: 0 auto;
    /* filter: grayscale(70%); */
  }

  .langbar__img_hide {
    display: none;
  }

  .about {
    padding: 10px;
  }

  .about__heading {
    font-weight: bold;
    font-size: 24px;
    text-align: left;
  }

  .about__text {
    font-size: 14px;
    text-align: left;
  }

  .about__img {
    width: 100%;
  }

  .about__skill__img {
    max-height: 24px;
  }

  .projects {
    width: 100%;
    margin: 0 auto;
    padding: 10px;
  }

  .projects__heading {
    font-family: "Oswald", sans-serif;
    text-align: left;
    font-size: 24px;
    font-weight: bold;
    padding-bottom: 25px;
  }

  .projects__story {
    margin: 10px;
    max-width: 200px;
    height: 320px;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 0px 0px 5px #888888;
  }

  .projects__story:hover {
    -webkit-filter: grayscale(60%);
            filter: grayscale(60%);
    box-shadow: 0px 0px 10px #888888;
  }

  .projects__link {
    color: rgb(58, 58, 58) !important;
    text-decoration: none !important;
  }

  .projects__img {
    height: 200px;
    width: 100%;
    max-width: 200px;
    object-fit: cover;
    margin: 0 auto;
    display: block;
    border-radius: 5px 5px 0px 0px;
  }

  .projects__title {
    font-size: 16px;
    font-family: "Oswald", sans-serif;
    margin-top: 0px;
    text-align: center;
    font-weight: bold;
  }

  .projects__text {
    text-align: center;
    font-size: 12px;
  }

  .contact {
    width: 80%;
    margin: 0 auto;
  }

  .contact__heading {
    font-family: "Oswald", sans-serif;
    text-align: left;
    font-size: 24px;
    font-weight: bold;
  }

  .contact__form {
    font-size: 16px;
  }

  .contact__form-group {
    font-size: 16px;
  }

  .contact__name {
    width: 100%;
  }

  .contact__control {
    font-size: 16px !important;
  }

  .contact__email {
  }

  .contact__message {
  }

  .contact__warning {
    color: red;
  }

  .copyright {
    text-align: center;
  }
}

